@import './styles/normalize.scss';
@import './styles/vars.scss';

.buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    margin-top: 200px;

    &>button {
        cursor: pointer;
        font-size: 18px;
        background: $logo-text;

        &:hover {
            background: $general-btn;
        }
    }
}