@import '../../styles/vars.scss';

.content {
    &__logo {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: $logo-bckg;
        color: $logo-text;
        padding: 10px;

        &>h3 {
            margin-right: 10px;
            cursor: pointer;

            &:hover {
                color: $general-btn;
            }
        }

        &-items {
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }
}

.weather {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;

    &__search {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__info {
        margin-top: 70px;
    }
}

.info {
    width: 500px;
    border: 3px solid $logo-bckg;
    background: $logo-text;

    &__coords {
        display: flex;
        gap: 10px;
        padding: 0 8px 0;
    }

    &__city {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 8px 0;
    }

    &__temperature {
        margin-top: 40px;

        &>h5 {
            padding: 0 5px 5px;
        }

        &-details {
            display: flex;
            align-items: center;

            &>h3 {
                margin-top: 3px;
            }
        }
    }
}

.loader,
.error {
    margin-top: 100px;
}